import React from "react";
import htmlAstScraper from "../../htmlAstScraper";
import { Link as GatsyLink } from "gatsby";

import "./styles.scss";

export const NavBar = ({htmlAst}) => {
    const set = new Set(['a']);
    const links = htmlAstScraper(htmlAst, set).filter(l => {
        return l.children.length > 0 && l.children[0].value;
    }).map(l => {
        return {
            text: l.children[0].value,
            href: l.properties.href,
        };
    });
    return (
        <nav className="NavBar">
            <ul>
                {links.map((l, idx) => {
                    return (
                        <li key={idx}>
                            <GatsyLink color='black.500' to={l.href}>{l.text}</GatsyLink>
                        </li>
                    );
                })}
            </ul>
        </nav>
    );
};

export default NavBar;